<template>
  <div>
    <v-container class="evaluacion__container">
      <title-ribbon
        :title="evaluacion.Titulo ? evaluacion.Titulo : '...'"
        :theme="'orange'"
      />
      <div
        v-if="charged"
        class="evaluacion_container__main"
      >
        <v-row class="evaluacion_row__img">
          <v-col>
            <v-img
              :src="
                `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1640182831/terapiacl/servicios/evaluacion/${evaluacion.Imagen}.png`
              "
            />
          </v-col>
        </v-row>
        <splitter :color="2" />
        <v-row class="evaluacion_row__descripcion">
          <v-col>
            <p v-html="evaluacion.Descripcion" />
          </v-col>
        </v-row>
        <v-row class="evaluacion_row__tests">
          <v-col
            v-if="evaluacion.Tests"
            class="col-12 tests"
          >
            <div class="main">
              <h4>{{ evaluacion.TestTitulo }}</h4>
              <div class="texts">
                <p
                  v-for="(test, t) in evaluacion.Tests"
                  :key="t"
                >
                  <strong v-html="test.Titulo+': '" />
                  <span v-html="test.Descripcion" />
                </p>
              </div>
            </div>
          </v-col>
        </v-row>
        <v-row class="evaluacion_row__otras-evaluaciones">
          <v-col
            v-if="evaluacion.OtrasEvaluacionesDelTipo"
            class="col-12 evaluaciones"
          >
            <div class="main">
              <h4>{{ evaluacion.EvaluacionesTitulo }}</h4>
              <div class="lists">
                <ul>
                  <li
                    v-for="(otra, o) in evaluacion.OtrasEvaluacionesDelTipo"
                    :key="o"
                  >
                    <router-link :to="`/servicios/evaluaciones/${otra.ToLink}`">
                      {{ otra.Titulo }}
                    </router-link>
                  </li>
                </ul>
              </div>
            </div>
          </v-col>
        </v-row>
        <splitter-volver :theme="'orange'" />
      </div>
      <div
        v-else
        class="text-center"
      >
        <v-progress-circular
          :size="70"
          :width="7"
          color="warning"
          indeterminate
        />
      </div>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  export default {
    name: 'Evaluacion',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data () {
      return {
        evaluacion: [],
        charged: false,
      }
    },
    watch: {
      '$route.params.toLink': {
        handler: function (toLink) {
          this.getEvaluacion(toLink)
        },
      },
    },
    created () {
      this.getEvaluacion(this.$route.params.toLink)
    },
    methods: {
      getEvaluacion (toLink) {
        this.charged = false
        Vue.axios
          .get(`/api/servicios/evaluacion/${toLink}`)
          .then(res => {
            this.evaluacion = res.data.evaluacion[0]
            // console.log(this.evaluacion)
            this.charged = true
          })
          .catch(error => {
            console.log(error)
          })
      },
    },
    metaInfo: {
      title: 'Evaluacion',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
